import {
    Component,
    NgModule,
    Host,
    SkipSelf,
    Input
} from '@angular/core';

import { NestedOptionHost } from '../../core/nested-option';
import { CollectionNestedOption } from '../../core/nested-option';


@Component({
    selector: 'dxi-menu-item',
    template: '',
    styles: [''],
    providers: [NestedOptionHost]
})
export class DxiMenuItemComponent extends CollectionNestedOption {
    @Input()
    get action() {
        return this._getOption('action');
    }
    set action(value: any) {
        this._setOption('action', value);
    }

    @Input()
    get text() {
        return this._getOption('text');
    }
    set text(value: any) {
        this._setOption('text', value);
    }

    protected get _optionPath() {
        return 'menuItems';
    }



    constructor(@SkipSelf() @Host() parentOptionHost: NestedOptionHost, @Host() optionHost: NestedOptionHost) {
        super();
        parentOptionHost.setNestedOption(this);
        optionHost.setHost(this, this._fullOptionPath.bind(this));
    }

}

@NgModule({
  declarations: [
    DxiMenuItemComponent
  ],
  exports: [
    DxiMenuItemComponent
  ],
})
export class DxiMenuItemModule { }

